.fa-1-5x{
  font-size: 15px;
}
a:hover, .navbar-default .navbar-nav > li > a:focus {
    color: $contraste !important;
    background-color: transparent !important;
    @include transition(400ms);
}
.navbar-default .navbar-brand:hover, .navbar-default .navbar-brand:focus {
    color: $contraste !important;
    background-color: transparent !important;
    @include transition(400ms);
}
.act-btn{
  display: flex;
  flex-flow: row;
  a{
    flex:1;
  }
}
.tbtn{
  width: 40%;
}
.main-section{
  margin: auto;
  width: 50%;
}
.Titulo{font-size:1.5em !important;}
/*** CLIENTES ***/
.panel-actions{
  padding: 5px 15px;
}
.panel-actions, .search-form{
  display: flex;
  flex-flow: row;
}
.search-text{flex: 3;}
.search-btn{flex: 1;}
.title{
  color: $texto1;
}
